import React from "react";
import { Paper } from "@material-ui/core";
import { withStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';
import EditIcon from '@material-ui/icons/Edit'
import DeleteIcon from '@material-ui/icons/Delete'

import ConsomableForm from '../Snipped/ConsommableForm'


export default class Add extends React.Component {
  constructor(props){
    super(props)

    this.state = {
      errors: {},
      technician : {
        value: '',
        error: false,
        errorMessage: null
      },
      system : {
        value: '',
        error: false,
        errorMessage: null
      },
      subSystem : {
        value: '',
        error: false,
        errorMessage: null
      },
      component : {
        value: '',
        error: false,
        errorMessage: null
      },
      task : {
        value: '',
        error: false,
        errorMessage: null
      },
      difficulties : {
        value: '',
        error: false,
        errorMessage: null
      },
      designation : {
        value: '',
        error: false,
        errorMessage: null
      },
      ref : {
        value: '',
        error: false,
        errorMessage: null
      },
      quantity : {
        value: '',
        error: false,
        errorMessage: null
      },
      operation : {
        value: '',
        error: false,
        errorMessage: null
      },
      maintenance : {
        value: '',
        checked: false,
        error: false,
        errorMessage: null
      },
      default : {
        value: '',
        checked: false,
        error: false,
        errorMessage: null
      },
      symptom : {
        value: '',
        checked: false,
        error: false,
        errorMessage: null
      },

      submited: false,
      consommables: [],
    }
  }

  handleChangetechnician(e){
    this.setState({
      technician: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  
  handleChangeSystem(e){
    this.setState({
      system: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  
  handleChangeComponent(e){
    this.setState({
      component: {
        value: e.target.value,
        error: false
      }
    })
  }
 
  
  handleChangeSubSystem(e){
    this.setState({
      subSystem: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  handleChangeTask(e){
    this.setState({
      task: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  handleChangeDifficulties(e){
    this.setState({
      difficulties: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  handleChangeDesignation(e){
    this.setState({
      designation: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  handleChangeRef(e){
    this.setState({
      ref: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  handleChangeQuantity(e){
    this.setState({
      quantity: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  handleChangeOperation(e){
    this.setState({
      operation: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  handleChangeMaintenance(e){
    this.setState({
      maintenance: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  handleChangeDefault(e){
    this.setState({
      default: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  handleChangeSymptom(e){
    this.setState({
      symptom: {
        value: e.target.value,
        error: false
      }
    })
  }

  _handleSubmit(e){
    e.preventDefault()

    var values = {
      technician: this.state.technician.value,
      system: this.state.system.value,
      subSystem: this.state.subSystem.value,
      component: this.state.component.value,
      task: this.state.task.value,
      difficulties: this.state.difficulties.value,
      ref: this.state.ref.value,
      designation: this.state.designation.value,
      quantity: this.state.quantity.value,
      operation: this.state.operation.value,
      maintenance: this.state.maintenance.value,
      default: this.state.default.value,
      symptom: this.state.symptom.value,
    }

    if( !values.system ){
      this.setState({
        system: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }
    if( !values.subSystem ){
      this.setState({
        subSystem: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }
    if( !values.component ){
      this.setState({
        component: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }
    if( !values.difficulties ){
      this.setState({
        difficulties: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }
    if( !values.task ){
      this.setState({
        task: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }
    if( !values.operation ){
      this.setState({
        operation: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }
    if( !values.maintenance ){
      this.setState({
        maintenance: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }
    if( !values.default ){
      this.setState({
        default: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }
    if( !values.symptom ){
      this.setState({
        symptom: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }

    if(
      (values.system) !== "" &&
      (values.subSystem) !== "" &&
      (values.component) !== "" &&
      (values.task) !== "" &&
      (values.difficulties) !== "" &&
      (values.operation) !== "" &&
      (values.maintenance) !== "" &&
      (values.default) !== "" &&
      (values.symptom) !== "" 
    ){
      this.setState({
        submited: true
      })
    }

    console.log("Values submited", values)
  }

  _handleAddConsommable(value){
    this.setState({
      consommables: [...this.state.consommables, value]
    })
  }


  render() {
    const { consommables, submited } = this.state

    console.log("State update", consommables);
    

    return (
    <div className="form-body">
      <section className="container">
          <Paper className={''} style={{ padding: 30 }}>
            {
              submited ? 
              <div className={`row`} style={{ paddingTop: 20 }}>
                <h2 className="text-center" style={{ width: "100%" }}>Formulaire envoyé avec succès</h2>
              </div>
              : 
              <div>
                <h2>Fiche de rapport N° { `002 ${ (new Date).toLocaleString() }` }</h2>
                <form noValidate autoComplete="off" onSubmit={ this._handleSubmit.bind(this) }>

                  <div className={`row`} style={{ paddingTop: 20 }}>
                    <div className="col-4">
                      <FormControl 
                        className={'form-control'}
                        error={ this.state.technician.error && this.state.technician.error }
                      >
                        <InputLabel id="technician-select">Technicien</InputLabel>
                        <Select
                          labelId="technician-select"
                          id="technician"
                          defaultValue={"technicien 1"}
                          disabled
                          value={this.state.technician.value}
                          onChange={this.handleChangetechnician.bind(this)}
                          fullWidth
                        >
                          <MenuItem value={"technicien 1"}>Technicien</MenuItem>
                        </Select>
                        { this.state.technician.error ? <FormHelperText>{this.state.technician.errorMessage}</FormHelperText> : null }
                      </FormControl>
                    </div>
                  </div>

                  <div className={`row`} style={{ marginTop: 40, border: "1px solid lightgrey", paddingTop: 10, paddingBottom: 20, borderRadius: 10 }}>
                    <h5 style={{ 
                        paddingBottom: 5, 
                        width: "100%", 
                        borderBottom: "1px solid lightgrey",
                        marginLeft: 20, 
                        marginRight: 20,
                      }}
                    >Localisation de l'intervention</h5>

                    <div className="col-4">
                      <TextField
                        id="system-error-helper-text"
                        label="Système"
                        error={ this.state.system.error && this.state.system.error }
                        name="system"
                        onChange={ this.handleChangeSystem.bind(this) }
                        placeholder={"Nom du produit"}
                        value={this.state.system.value}
                        type="text"
                        helperText={ this.state.system.error ? this.state.system.errorMessage : null }
                        required
                        fullWidth
                      />
                    </div>
                    <div className="col-4">
                      <TextField
                        id="fonctional-helper-text"
                        label="Sous-ensemeble fonctionnel"
                        error={ this.state.subSystem.error && this.state.subSystem.error }
                        name="sub-system"
                        onChange={ this.handleChangeSubSystem.bind(this) }
                        placeholder={"Sous ensemble fonctionnel"}
                        value={this.state.subSystem.value}
                        type="text"
                        helperText={ this.state.subSystem.error ? this.state.subSystem.errorMessage : null }
                        required
                        fullWidth
                      />
                    </div>
                    <div className="col-4">
                      <TextField
                        id="component-error-helper-text"
                        label="Composant"
                        error={ this.state.component.error && this.state.component.error }
                        name="component"
                        onChange={ this.handleChangeComponent.bind(this) }
                        placeholder={"Nom du composant"}
                        value={this.state.component.value}
                        type="text"
                        helperText={ this.state.component.error ? this.state.component.errorMessage : null }
                        required
                        fullWidth
                      />
                    </div>
                  </div>

                  <div className={`row`} style={{ marginTop: 40, border: "1px solid lightgrey", paddingTop: 10, paddingBottom: 20, borderRadius: 10 }}>
                    <h5 style={{ 
                        paddingBottom: 5, 
                        width: "100%", 
                        borderBottom: "1px solid lightgrey",
                        marginLeft: 20, 
                        marginRight: 20,
                        marginBottom: 20,
                      }}
                    >Détails du travail</h5>
                    <div className="col-12" style={{ marginBottom: 10 }}>
                      <TextField
                        id="task-to-do-description"
                        label="Travail à éffectué"
                        type="text"
                        rows={3}
                        error={ this.state.task.error && this.state.task.error }
                        value={this.state.task.value}
                        onChange={ this.handleChangeTask.bind(this) }
                        helperText={ this.state.task.error ? this.state.task.errorMessage : null }
                        variant="outlined"
                        multiline
                        required
                        fullWidth
                      />
                    </div>
                    <div className="col-12">
                      <TextField
                        id="difficulties-description"
                        label="Difficultés rencontrées"
                        type="text"
                        rows={3}
                        error={ this.state.difficulties.error && this.state.difficulties.error }
                        value={this.state.difficulties.value}
                        onChange={ this.handleChangeDifficulties.bind(this) }
                        helperText={ this.state.difficulties.error ? this.state.difficulties.errorMessage : null }
                        variant="outlined"
                        multiline
                        required
                        fullWidth
                      />
                    </div>
                  </div>

                  <div className={`row`} style={{ marginTop: 40, border: "1px solid lightgrey", paddingTop: 10, paddingBottom: 20, borderRadius: 10 }}>
                    <h5 style={{ 
                        paddingBottom: 5, 
                        width: "100%", 
                        borderBottom: "1px solid lightgrey",
                        marginLeft: 20, 
                        marginRight: 20,
                      }}
                    >Pièces de rechange et consommable</h5>

                    
                    {
                    consommables.length !== 0 ? (
                      <div className={`row`} style={{ width: "100%" }}>
                        <div style={{ width: "100%", padding: "0 20px 20px 20px", }}>
                          <table className="table table-striped">
                            <thead>
                              <tr>
                                <th scope="col">N°</th>
                                <th scope="col">Désignation</th>
                                <th scope="col">Reférence</th>
                                <th scope="col">Quantité</th>
                                <th scope="col">Actions</th>
                              </tr>
                            </thead>
                            <tbody>
                              {
                                consommables.map((val,key) => 
                                <tr key={key}>
                                  <th scope="row">{val.id}</th>
                                  <th scope="row">{val.designation}</th>
                                  <td>{val.reference}</td>
                                  <td>{val.quantity}</td>
                                  <td style={{ paddingLeft: 0, paddingTop: 0 }}>
                                    <Button 
                                      className='btn btn-link'
                                      // onClick={ (e) =>  e.preventDefault(), this._handleEditVariety.bind(this, val.id) }
                                    >
                                      <EditIcon />
                                    </Button>
                                    &nbsp;&nbsp;&nbsp;
                                    <Button 
                                      className='btn btn-link'
                                      // onClick={ (e) =>  e.preventDefault(), this._handleDeleteVariety.bind(this, val.id) }
                                    >
                                      <DeleteIcon />
                                    </Button>
                                  </td>
                                </tr>)
                              }
                            </tbody>
                          </table>
                        </div>
                      </div>
                    ) : null
                  }
                    
                    <ConsomableForm addConsommable={ this._handleAddConsommable.bind(this) } />
                      
                  </div>

                  
                  <div className={`row`} style={{ marginTop: 30 }}>
                    <div className="col-4">
                      <FormControl 
                        className={'form-control'}
                        style={{ width: "100%"  }}
                        error={ this.state.operation.error && this.state.operation.error }
                      >
                        <InputLabel id="operation-select-select">Operations</InputLabel>
                        <Select
                          labelId="operation-select-select"
                          id="operation-select"
                          value={this.state.operation.value}
                          onChange={this.handleChangeOperation.bind(this)}
                          fullWidth
                        >
                          <MenuItem value={"operation 1"}>Operation 1</MenuItem>
                          <MenuItem value={'operation 2'}>Operation 2</MenuItem>
                          <MenuItem value={"operation 3"}>Operation 3</MenuItem>
                        </Select>
                        { this.state.operation.error ? <FormHelperText>{this.state.operation.errorMessage}</FormHelperText> : null }
                      </FormControl>
                    </div>
                    <div className="col-4">
                      <FormControl 
                        className={'form-control'}
                        style={{ width: "100%"  }}
                        error={ this.state.maintenance.error && this.state.maintenance.error }
                      >
                        <InputLabel id="maintenace-type-select">Type de maintenance</InputLabel>
                        <Select
                          labelId="maintenace-type-select"
                          id="maintenace-type"
                          value={this.state.maintenance.value}
                          onChange={this.handleChangeMaintenance.bind(this)}
                          fullWidth
                        >
                          <MenuItem value={"maintenance 1"}>Maintenance 1</MenuItem>
                          <MenuItem value={'maintenance 2'}>Maintenance 2</MenuItem>
                          <MenuItem value={"maintenance 3"}>Maintenance 3</MenuItem>
                        </Select>
                        { this.state.maintenance.error ? <FormHelperText>{this.state.maintenance.errorMessage}</FormHelperText> : null }
                      </FormControl>
                    </div>
                    <div className="col-4">
                      <FormControl 
                        className={'form-control'}
                        style={{ width: "100%"  }}
                        error={ this.state.default.error && this.state.default.error }
                      >
                        <InputLabel id="default-cause-select">Cause de défaillance</InputLabel>
                        <Select
                          labelId="default-cause-select"
                          id="default-cause"
                          value={this.state.default.value}
                          onChange={this.handleChangeDefault.bind(this)}
                          fullWidth
                        >
                          <MenuItem value={"default 1"}>Defaillance 1</MenuItem>
                          <MenuItem value={'default 2'}>Defaillance 2</MenuItem>
                          <MenuItem value={"default 3"}>Defaillance 3</MenuItem>
                        </Select>
                        { this.state.default.error ? <FormHelperText>{this.state.default.errorMessage}</FormHelperText> : null }
                      </FormControl>
                    </div>
                  </div>
                  <br />
                  <div className={`row`} style={{ paddingTop: 20 }}>
                    <div className="col-12">
                      <TextField
                        id="symptom-description"
                        label="Symptômes liés à la défaillance"
                        type="text"
                        rows={3}
                        error={ this.state.symptom.error && this.state.symptom.error }
                        value={this.state.symptom.value}
                        onChange={ this.handleChangeSymptom.bind(this) }
                        helperText={ this.state.symptom.error ? this.state.symptom.errorMessage : null }
                        variant="outlined"
                        multiline
                        required
                        fullWidth
                      />
                    </div>
                  </div>
                  
                  <div className={`row`} style={{ paddingTop: 30 }}>
                    <div className={`mr-auto mx-auto`}>
                      <Button 
                        variant="contained" 
                        color="primary"
                        type="submit"
                      >
                        Enregistrer
                      </Button>
                      &nbsp;&nbsp;&nbsp;
                      <Button variant="contained">
                        Annuler
                      </Button>
                    </div>
                  </div>
                </form>
              </div>
            }
            
          </Paper>
        </section>
    </div>
    );
  }

  
}
