import React from "react";
import { Paper } from "@material-ui/core";
import { withStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Button from '@material-ui/core/Button';
import Switch from '@material-ui/core/Switch';

import Icon from '@material-ui/core/Icon';


export default class Add extends React.Component {
  constructor(props){
    super(props)

    this.state = {
      errors: {},
      
      designation : {
        value: '',
        error: false,
        errorMessage: null
      },
      reference : {
        value: '',
        error: false,
        errorMessage: null
      },
      quantity : {
        value: '',
        error: false,
        errorMessage: null
      },
      
      initial_id: 1,
      consommables: [],
    }
  }
  
  
  handleChangeDesignation(e){
    this.setState({
      designation: {
        value: e.target.value,
        error: false
      }
    })
  }
  
  
  handleChangeReference(e){
    this.setState({
      reference: {
        value: e.target.value,
        error: false
      }
    })
  }
 
  
  handleChangeQuantity(e){
    this.setState({
      quantity: {
        value: e.target.value,
        error: false
      }
    })
  }  


  _handleSubmit(e){
    e.preventDefault()

    var values = {
      reference: this.state.reference.value,
      designation: this.state.designation.value,
      quantity: this.state.quantity.value,
    }

    if( !values.designation ){
      this.setState({
        designation: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }
    if( !values.reference ){
      this.setState({
        reference: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }
    if( !values.quantity ){
      this.setState({
        quantity: {
          error: true,
          errorMessage: "Selectionner une valeur"
        },
      })
    }
    

    if(
      (values.designation) !== "" &&
      (values.reference) !== "" &&
      (values.quantity) !== ""
    ){
      var consommable = {
        id: this.state.initial_id,
        designation: values.designation,
        reference: values.reference,
        quantity: values.quantity,
      }

      this.setState((state) => ({
        initial_id: (state.initial_id + 1)
      }))

      this.props.addConsommable(consommable)
      this.setState({
        designation : {
          value: '',
          error: false,
          errorMessage: null
        },
        reference : {
          value: '',
          error: false,
          errorMessage: null
        },
        quantity : {
          value: '',
          error: false,
          errorMessage: null
        },
      })
    }

    console.log("Values submited", values)
  }


  render() {


    return (
    <div className="form-body" style={{ width: "100%" }}>

      <form noValidate autoComplete="off">  
        <div className="row" style={{ marginLeft: 0, marginRight: 0 }}>
          <div className="col-4">
            <TextField
              id="designation-error-helper-text"
              label="Désignation"
              error={ this.state.designation.error && this.state.designation.error }
              name="designation"
              onChange={ this.handleChangeDesignation.bind(this) }
              placeholder={"Désignation"}
              value={this.state.designation.value}
              type="text"
              helperText={ this.state.designation.error ? this.state.designation.errorMessage : null }
              required
              fullWidth
            />
          </div>
          <div className="col-4">
            <TextField
              id="ref-error-helper-text"
              label="Reférence"
              error={ this.state.reference.error && this.state.reference.error }
              name="ref"
              onChange={ this.handleChangeReference.bind(this) }
              placeholder={"Reférence"}
              value={this.state.reference.value}
              type="text"
              helperText={ this.state.reference.error ? this.state.reference.errorMessage : null }
              required
              fullWidth
            />
          </div>
          <div className="col-3">
            <TextField
              id="quantity-error-helper-text"
              label="Quantité"
              error={ this.state.quantity.error && this.state.quantity.error }
              name="quantity"
              onChange={ this.handleChangeQuantity.bind(this) }
              placeholder={"Quantité"}
              value={this.state.quantity.value}
              type="text"
              helperText={ this.state.quantity.error ? this.state.quantity.errorMessage : null }
              required
              fullWidth
            />
          </div>
          <div className="col-1" style={{ paddingTop: 20 }}>
            <Button 
              variant="outlined" 
              color="default"
              onClick={ this._handleSubmit.bind(this) }
            >
              <Icon  className='form_icon' style={{ fontSize: 30, float: "right" }}>add_circle</Icon>
            </Button>
          </div>
        </div>
      </form>
    </div>
    );
  }

  
}
